.wrapperItemLastNew {
    height: max-content;
    width: 100%;
    display: flex;
    padding: 10px;
    border: 2px solid grey;
    border-radius: 20px;
    margin-bottom: 10px;
    position: relative;

    svg {
        top: 10px;
        right: 10px;
        height: 15px;
        width: 15px;
        position: absolute;
        cursor: pointer;
    }
    &:last-child {
        margin-bottom: 0;
    }

    &_img {
        height: auto;
        width: 100%;
        max-width: 230px;
        background-position: center;
        background-repeat: no-repeat;
        background-size: cover;
        margin-right: 10px;
        border-radius: 10px;
    }

    &_content {
        &_headerTitle {
            width: 500px;
            font-size: 18px;
            font-weight: 600;
            margin-bottom: 10px;
        }

        &_author {
            margin-bottom: 5px;
            span {
                font-weight: 600;
            }
        }

        &_description {
            margin-bottom: 5px;
            span {
                font-weight: 600;
            }
        }
        
        &_time {
            margin-bottom: 5px;
            span {
                font-weight: 600;
            }
        }
    }
}
